// @flow
import React from 'react';
import { Link } from 'react-router-dom';
import InputEmail from '../components/InputEmail';

const styles_suscribite = {
    content: {
        position: 'relative',
        flexDirection: 'row',
        justifyContent: 'center',
        textAlign: 'center',
        left:0,
        top: 50,
        marginBottom:280,
    },
    title: {
        position: 'relative',
        flexDirection:'row',
        justifyContent: 'center',
        textAlign: 'center',
        fontSize: 15,
        fontWeight: 'normal',
        top: 0,
        left: 50,
        zIndex: 0,
        color:'gray'
    },
    titleuRL: {
        flexDirection:'row',
        justifyContent: 'center',
        position: 'relative',
        textAlign: 'center',
        fontSize: 20,
        fontWeight: 'normal',
        top: -10,
        left: 0,
        zIndex: 0,
        color:'black'
    },
    notice: {
        flexDirection:'row',
        justifyContent: 'center',
        position: 'relative',
        textAlign: 'center',
        fontSize: 12,
        fontWeight: 'normal',
        top: -10,
        left: 0,
        zIndex: 90,
        color:'gray'
    },
};
type ItemsSuscribite = {
    key:number;
    title: string;
    titleuRL: string;
    suscribite: string;
    url: string;
    colmns: string;
    width: number;
    height: number;
};

const SuscribiteRow = ({items }:ItemsSuscribite): React$Element<React$FragmentType> => {
    return (
    <React.Fragment>
                    <Link to="#">
                        <div className="navbar-suscribite">
 
                                    <div className="title">
                                    <p> {items.title}</p>
                                    </div>
                                    <div className="suscribite">
                                       {items.titleuRL}
                                    </div>  
                                        <div style={styles_suscribite.notice}>
                                        {items.notice}
                                            </div>
                                        
                                        <InputEmail/>
                                     
                        </div>
                    </Link>
         </React.Fragment>
    );
};

export default SuscribiteRow;
